      h2 8.1 Control de plagas 
    p El control de plagas o manejo de plagas tiene el objetivo de regular una especie a travs de diferentes tipos de medidas, para poder comprender es necesario revisar algunos conceptos:
    TabsC.color-acento-contenido
      .py-3.py-md-4(titulo="Plaga")
        .row.align-items-center

          .col-md-6
            figure
              img(src='@/assets/curso/tema8/3.png', alt='Texto que describa la imagen')
              figcaption Nota. Gua para la gestin de residuos peligrosos. Universidad Pontificia Bolivariana. (2011, p.71) 
          .col-md-6.mb-4.mb-md-0
            h4 Plaga
            p.mb-0 Una plaga es cualquier organismo vivo que cause dao o molestias, o que transmita o provoque una enfermedad.
            p Las plagas pueden ser animales, plantas o bacterias. Una plaga tambin puede ser cualquier cosa que simplemente se encuentre en un lugar no deseado, por ejemplo, el trbol en una zona de juegos cubierta por csped. Las ratas, los ratones, las cucarachas, las moscas domsticas, los mapaches, las ardillas, las hormigas, la maleza y las bacterias son ejemplos de diferentes tipos de plagas. Asegrese de que realmente se trata de una plaga antes de eliminarla, recuerde que no todas las plagas son vectores quiere decir generan enfermedades algunos son por estar en un lugar que no deberan, recuerde que existen plagas que no puedo matar y usar pesticidas ejemplo: los murcilagos, las palomas, las ardillas, pero otras si permiten su exterminio ejemplo: las cucarachas, las ratas. Universidad de california (2011, p. 7) 
      .py-3.py-md-4(titulo="Pesticida")
        .row.align-items-center
          h5.mb-1 Figura 25 
          p Ejemplo de plagas
          .col-md-6
            figure
              img(src='@/assets/curso/tema8/2.png', alt='Texto que describa la imagen')
          .col-md-6.mb-4.mb-md-0
            h4 Pesticida
            p El pesticida es un veneno creado para exterminar o controlar organismos vivos como maleza, bichos, araas o cualquier otra cosa que usted no quiera que viva en su establecimiento. Los aerosoles contra las cucarachas y las hormigas, las bombas contra las pulgas, los raticidas, los herbicidas y las bolas de naftalina son ejemplos de pesticidas. Todos los pesticidas son potencialmente dainos. Los pesticidas deben ser usados como ltimo recurso. Los pesticidas en aerosol y los nebulizadores (bombas contra insectos) son los ms dainos y se debe evitar su uso preferiblemente por salud. Universidad de california (2011, p. 8) 
      .py-3.py-md-4(titulo="Tipo de pesticidas")
        .row.align-items-center

