                    td.bgg 1. Insecticidas<br>2. Herbicidas<br>3. Veneno para matar roedores<br>4. Fungicidas<br>5. Bactericidas<br>6. Alguicidas<br>7. Repelentes de insectos<br>8. Molusquicidas

                    td.bgg Insectos y plagas relacionadas como las araas<br>Maleza y otras plantas no deseadas<br>Ratones, ratas y otros roedores.<br>Moho y otros hongos<br>Bacterias<br>Algas (en piscinas y otras fuentes de agua)<br>Mosquitos y otros insectos o garrapatas<br>Caracoles y babosas.
      .py-3.py-md-4(titulo="Control de plagas")
        .row.align-items-center
          .col-md-6
            figure
              img(src='@/assets/curso/tema8/5.png', alt='Texto que describa la imagen')
          .col-md-6.mb-4.mb-md-0
            h4 Control de plagas
            p Si las plagas se vuelven un problema, necesitar controlarlas o exterminarlas, fomente el uso de materiales y mtodos para tratar las plagas que maximicen la seguridad y reduzcan la exposicin al personal a productos qumicos dainos. Para controlar las plagas, elija mtodos que sean: 
            p.mb-0 Los menos dainos para la salud humana y otros seres vivos que no son plagas. 
            p.mb-0 - De mayor duracin posible y eviten reincidencia.
            p.mb-0 - Los ms fciles de implementar y que sean seguros y efectivos.
            p - Los ms econmicos a corto y largo plazo u adecuados para la plaga en particular y que coincidan con el estado del ciclo de vida de la plaga cuando est en una etapa ms vulnerable. 
            p A menudo se pueden controlar las plagas siguiendo pasos libres de qumicos como:
            p.mb-0 - Evitar que las plagas entren eliminando las fuentes de comida, agua y refugio.
            p.mb-0 - Lavar el rea con agua y jabn.
            p - Usar una aspiradora para retirarlas: una aspiradora de aire de alta eficacia (HEPA) con un filtro fino que pueda eliminar los excrementos y las partes de los insectos.
          p - Usar trampas: algunas trampas se usan principalmente para supervisar la presencia de plagas. Entre stas se incluyen las trampas para cucarachas y las trampas en base a feromona (que atraen sexualmente a los insectos) an si la infestacin es leve, estas trampas pueden ser usadas para mantener las plagas bajo control.  Otras trampas libres de qumicos incluyen:
          p Trampas para ratones y ratas que se cierran mecnicamente. Colquelas en los lugares donde los roedores puedan encontrarlas. 
          p Matamoscas y trampas de luz ultravioleta para moscas. Trampas cnicas para moscas o avispas de chaqueta amarilla. Trampas de caja para zorrillos, mapaches y comadrejas (zarigeyas) Si los mtodos libres de qumicos no dan resultados, use el pesticida menos daino para tratar el problema de plaga. Los pesticidas menos dainos son: cebos que se colocan en lugares fijos (no rociados o esparcidos)  efectivos contra plagas especficas. Son de baja toxicidad aguda y crnica para las personas, los animales y el medio ambiente. se biodegradan rpidamente. Exterminan una categora limitada de plagas especficas. tienen poco o ningn impacto en otros organismos, especialmente en los predadores de plagas.
