    h4.mt-4 Qu dao puede ocasionar los desinfectantes clorados a la salud y al medio ambiente?
    p La intoxicacin por hipocloritos y por sustancias liberadoras de cloro puede estar acompaada de los siguientes sntomas:  
    .row.justify-content-center
      .col-lg-10
        .row.p-3.colort8
          ul.lista-ul--color
            li 
              i.fas.fa-check
              | Dolor e inflamacin de la boca, la faringe, el esfago y el estmago, seguidos por erosin de las mucosas, en especial del estmago. Vmito, que puede ser de tipo sanguinolento. 
            li 
              i.fas.fa-check
              | Colapso circulatorio, piel fra y paro respiratorio.
            li 
              i.fas.fa-check
              | Confusin, delirio y coma.
            li 
              i.fas.fa-check
              | Edema de la faringe, glotis y laringe, en algunos casos con obstruccin.
            li 
              i.fas.fa-check
              | Perforacin del esfago o el estmago, con peritonitis.
