              p.text-center.mt-4 Listado de residuos generados con mayor frecuencia en la organizacin. 
    p.mt-3 <strong>Para mayor facilidad se puede usar la siguiente tabla de informacin como ejemplo que sirve para enlistar los residuos, identificar el tipo de residuo, el recipiente a usar, el rtulo a usar, la capacidad del mismo, el estado del recipiente y si es posible pueden ir ms datos como reas y ubicacin especfica del punto ecolgico</strong>, tambin estas tablas colocan una casilla adicional que es que se hace con este, y que gestor ambiental lo gestiona. Manual para el manejo integral de residuos slidos en el valle de aburr. rea Metropolitana del Valle de Aburr (2016, p. 18)
