    p.mt-4 <strong>En cuanto a los residuos de tipo qumico, es preferible manejarlos en sus propios envases, empaques y recipientes, atendiendo las instrucciones dadas en sus etiquetas y hojas de seguridad</strong>, las cuales deben ser suministradas por los proveedores, cuidando de no mezclarlos cuando sean incompatibles o reaccionen entre s. En estos casos, se debe consultar normas de seguridad industrial y salud ocupacional. 
