          p El envasado, embalado, rotulado y etiquetado debe estar conforme a la normatividad vigente, principalmente cuando se trate de residuos peligrosos. Manual para el manejo integral de residuos slidos en el Valle de Aburr. rea metropolitana del Valle de Aburr (2016, p.20)
