      h2 5.1 Cdigo de colores en los recipientes
    p Para implementar la separacin de residuos, se utiliza un cdigo de colores en los recipientes para su identificacin, usualmente este cdigo de colores se rige a nivel territorial actualmente se cuenta con una norma regulatoria que es la Resolucin No. 2184 de 2019 ARTCULO 4o. Adptese en el territorio nacional, el cdigo de colores para la separacin de residuos slidos en la fuente, as:
