          .col-lg-4
            .row.p-3.colort3
              p Algunas empresas tienen como gua la GTC 24 de 2009 para sus procesos de gestin de residuos, ya que algunos residuos no se disponen al servicio pblico de aseo lo cual le da la opcin de clasificar de manera ms especfica los residuos sobre todos los de reciclaje y entregarlos desde la fuente ms separados a gestores ambientales de reciclaje y tratamiento de residuos.
              p Esta GTC 24 no es obligatoria es opcional, algunas empresas tambin por eleccin utilizan solo algunos colores de esta GTC 24.
