      h2 5.2 Caractersticas de los recipientes
    p Los tipos y capacidades de los recipientes dependen de las caractersticas y tipos de residuos, el tipo y frecuencia del sistema de recoleccin y del espacio disponible para ellos.
    p Frecuentemente se emplean bolsas, contenedores, canecas, cajas, entre otros. Sin embargo, se debe tener en cuenta lo siguiente, para su seleccin:
    .row.justify-content-center
      .col-lg-10
        .row.p-4.colort53
          .row.linea.align-items-center 
            .col-1
              img.wi2(src='@/assets/curso/tema5/q2.svg' alt='AvatarTop')
            .col-11
              p <strong>A nivel domstico</strong> se espera que los recipientes sean de material impermeable, liviano y resistente, de fcil carga, de tal forma que facilite su transporte y se reduzca el impacto sobre el ambiente y la salud humana.
          .row.linea.mt-3.align-items-center
            .col-1
              img.wi2(src='@/assets/curso/tema5/q3.svg' alt='AvatarTop')
            .col-11
              p <strong>Los recipientes de carcter retornable</strong>  deberan permitir su fcil limpieza.
          .row.linea.mt-3.align-items-center
            .col-1
              img.wi2(src='@/assets/curso/tema5/q4.svg' alt='AvatarTop')
            .col-11
              p <strong>Los recipientes que se empleen para presentar los residuos para recoleccin</strong>   deberan evitar el contacto de los residuos con el entorno y con las personas encargadas de dicha recoleccin.
          .row.linea.mt-3.align-items-center
            .col-1
              img.wi2(src='@/assets/curso/tema5/q5.svg' alt='AvatarTop')
            .col-11
              p <strong>Los recipientes tanto desechables como retornables deberan:</strong>    proporcionar seguridad e higiene, permitir el aislamiento de los residuos con el entorno, tener una capacidad y volumen proporcional al peso, volumen y caractersticas de los residuos contenidos, ser de material resistente, preferiblemente reciclable, reutilizable o ambos y facilitar su cierre o amarre.
          .row.linea.mt-3.align-items-center
            .col-1
              img.wi2(src='@/assets/curso/tema5/q6.svg' alt='AvatarTop')
            .col-11
              p <strong>En instalaciones multiusuario,</strong>  comerciales e industriales pueden emplearse grandes contenedores de acopio.
    p.mt-5 GTC 24- Gestin Ambiental Residuos slidos Gua para la separacin en la fuente. ICONTEC (2009, p.8)
    p Los recipientes para residuos infecciosos deben ser del tipo tapa y pedal y se identifican sus canecas con el logo de infeccioso, si es qumico algunas veces colocan el logo de una calavera y dicen el tipo de qumico que se envasa. 
