      h2 5.5 Rotulacin
    p Los recipientes deben ir rotulados con el nombre del generador, departamento, rea o servicio al que pertenecen, el nombre del residuo que contienen, fecha de recoleccin y el smbolo de peligrosidad si es residuo peligroso.
