        p.mb-0 Para la rotulacin de los residuos peligrosos se realiza acorde a la normativa ICONTEC (2005) Norma tcnica colombiana NTC1692 Transporte de mercancas peligrosas definiciones, clasificacin, marcado, etiquetado y rotulado y el decreto 1609 de 2002 "Por el cual se reglamenta el manejo y transporte terrestre automotor de mercancas peligrosas por carretera".
          br
          br
          | El etiquetado tiene como objetivo principal identificar el residuo peligroso y reconocer la naturaleza del peligro que representa, alertando a las personas involucradas en el transporte o manejo sobre las medidas de precaucin y prohibiciones. La etiqueta que se utilizar para la identificacin de los residuos puede ser de diferentes estilos.
